/************************************************
 * Category / Subcategories
 ************************************************/
.widget-header-breadcrumb-category { background-color: $color-bg; font-size: $font-body-sm; height: 30px; line-height: 30px;
    .wrap-width { align-items: center; display: flex; }

    .back { @include transition-fast; border-left: 1px solid $color-gray-1; border-right: 1px solid $color-gray-1; text-align: center; width: 30px; 
        &:hover { background-color: $color-bg-1; }
    }
    .category { @include transition-fast; font-weight: bold; margin: 0 20px; 
        &:hover { color: $color-primary; }
    }
    .sub-list { display: flex; list-style: none; }
    .sub-item { 
        &::after { color: $color-text-gray-3; content: '/'; display: inline-block; margin: 0 5px; }
        &:last-child::after { content: normal; }
        &.current .sub-link { color: $color-primary; font-weight: bold; }
    }
    .sub-link { @include transition-fast; 
        &:hover { color: $color-primary; }
    }

}

/* More items icon */
.menu-more-dots { margin-left: auto;
    .icon { display: inline-block; margin-right: 5px !important;
        span { @include transition-fast;  background-color: $color-gray-3; border-radius: 3px; display: inline-block; height: 6px; width: 6px; }
    }
    .label { position: relative; top: 2px; }

    &:hover {
        .icon span { background-color: $color-primary; }
    }
}


/************************************************
 * You are here
 ************************************************/
.widget-header-breadcrumb { font-size: $font-body-sm; line-height: 1.5em; padding: 16px 0;

    .breadcrumb-list { display: inline-block; margin-left: 5px; }
    .breadcrumb-entry { @include transition-fast;
        &:hover { color: $color-primary; }
        &::after { color: $color-primary; content: '\00BB'; display: inline-block; font-weight: bold; margin: 0 5px; }
        &:last-child::after { content: normal; }
    }
}