.widget-carousel-matches { margin-bottom: 0; margin-top: 10px;

    &:last-child { margin-bottom: 10px; }

    .content { margin-left: 40px; margin-right: 40px; }
    .match-entry { background-color: $color-bg-2; font-size: $font-body-sm; margin: 0 0.5px; position: relative; 
        &>* { position: relative; z-index: 1; }
    }
    .match-upcoming::before { background-color: $color-bg-1; content: ''; height: 50%; left: 0; position: absolute; top: 0; width: 100%; z-index: 0; }

    .match-week { color: $color-text-secondary; padding-top: 2px; text-align: center; }
    .match-teams { display: flex; padding: 3px 7px 8px; }
    .team-logo { max-height: 30px; max-width: 30px; }

    .match-result { flex: 1; position: relative; top: 5px; text-align: center; }
    .result-content { font-size: $font-heading-xs; font-weight: bold; line-height: 1em; }
    .result-status { color: $color-text-secondary; line-height: 1em; margin-top: 2px; }

    .match-teams-names { display: flex; }
    .match-finished { background-color: $color-finished; color: $color-text-inverted; }
    .match-live { background-color: $color-live; color: $color-text-inverted; }

    .team-name { line-height: 1.4em; flex: 1; padding: 0 5px; }
    .team-home { text-align: left; }
    .team-away { text-align: right; }

    .arrows button { width: 40px; }


    /* Tennis Matches */
    .match-tennis-finished, .match-tennis-live { height: 82px; position: relative;
        &::after { bottom: 0; content: ''; height: 10px; left: 0; position: absolute; width: 100%; }
    }
    .match-tennis-finished::after { background-color: $color-finished; }
    .match-tennis-live::after { background-color: $color-live; }

    .match-tennis-player { align-items: center; display: flex; margin-top: 2px; padding-left: 5px;
        &:first-of-type { margin-top: 4px; }

        .name { flex: 1; line-height: 1em; }
        .point-list { display: flex; }
        .point { background-color: $color-bg; height: 20px; line-height: 20px; margin-right: 2px; text-align: center; width: 20px; }

        .won { font-weight: bold; }
        .current { color: $color-live; font-weight: bold; }
    }

}