/************************************************
 * Large image with text overlay
 ************************************************/
.widget-news-featured { margin-top: 30px; position: relative;
    
    div { position: relative; }
    .title { background-color: $color-text-opaque; color: $color-text-inverted; left: 5px; line-height: 1.2em; padding: 30px; position: absolute; top: 0; z-index: 1; }
    .photo { display: block; }
    .credits { background-color: $color-text-opaque; bottom: 0; color: $color-text-inverted; line-height: 1em; padding: 3px 10px; position: absolute; right: 0; z-index: 1; }

    /* As in Category */
    &.bottom-text {
        .title { bottom: 0; left: 0; top: auto; }
    }
}