.widget-carousel-5 { margin-top: 30px; position: relative;

    .slick-slide { margin: 0 5px; }
    .content { display: flex; margin-left: 30px; margin-right: 30px; }
    .entry { flex: 1; position: relative; 
        &:hover .title { background-color: $color-primary; }
    }
    .live { background-color: $color-primary; color: $color-text-inverted; left: 0; padding: 5px 8px; position: absolute; top: 0; z-index: 1;
        .icon { left: 10px; top: 10px; vertical-align: middle; }
        .label { display: inline-block; line-height: 1em; margin-left: 2px; position: relative; top: 2px; text-transform: uppercase; }
    }
    .image { background-position: center; background-repeat: no-repeat; background-size: cover; padding-bottom: 60%; }
    .title { @include transition-fast; background-color: $color-text-opaque; bottom: 0; color: $color-text-inverted; font-size: $font-body-sm; font-weight: bold; left: 0; line-height: 1.1em; padding: 5px 10px; position: absolute; right: 0; z-index: 1; }

    .arrows {
        button { @include transition-fast; background: none; background-position: center; background-repeat: no-repeat; border: none; display: block; height: 100%; padding: 0; position: absolute; top: 0; width: 30px; }    
        .prev { background-image: url(../image/icon/ar_previous_normal.png); left: 0; 
            &:hover { background-image: url(../image/icon/ar_previous_hover.png); }
        }
        .next { background-image: url(../image/icon/ar_next_normal.png); right: 0; 
            &:hover { background-image: url(../image/icon/ar_next_hover.png); }
        }
    }
}
