/* Search results form */
.widget-search-form { display: flex; justify-content: center;

	.icon { height: 41px; }
	.keywords { background: none; border: none; border-bottom: 1px solid $color-gray-1; height: 41px; line-height: 41px; margin: 0 20px; padding: 0 10px; width: 490px; }
	.submit { margin: 0; }
}

/* Player profile in search results */
.widget-player-profile { background-color: $color-bg; display: block; margin-top: 30px; padding: 20px 30px 70px; position: relative;

	.profile-image { display: inline-block; position: relative;
		.photo { border-radius: 75px; display: block; max-height: 150px; max-width: 150px; }
		.number { @include circle(30); background-color: $color-primary; bottom: 0; color: $color-text-inverted; font-size: $font-body-md; font-weight: bold; position: absolute; right: 0; }
	}

	.rating { align-items: center; bottom: 0; display: flex; left: 0; padding: 20px; position: absolute;
		.value { background-color: $color-primary; color: white; font-size: $font-heading-md; font-weight: bold; line-height: 1em; margin-right: 10px; padding: 10px; }
	}

	.profile-properties { margin-top: 10px;

		.entry { line-height: 1.5em; margin-bottom: 10px; }
		.label { display: inline-block; text-align: right; width: 180px; }
		.divider { @include circle(6); background-color: $color-primary; margin: 0 18px; vertical-align: middle; }
		.content { display: inline-block; font-weight: bold; width: 180px; }
	}

	.widget-view-more { bottom: 0; position: absolute; right: 0; }
}