/************************************************
 * Global navigation
 ************************************************/
.widget-header-nav { align-items: center; display: flex;

    /* Top level (immediately visible) */
    .top { display: flex; margin-left: 20px; }
    .top-link { @include transition-fast; display: block; font-size: $font-body-xm; font-weight: 600; padding: 14.5px; 
        &:hover, &.current { background-color: $color-primary; }
    }
    /* Top level remains highlighted while moving around its dropdown content */
    .top-item:hover .top-link { background-color: $color-primary; }

}

/* Dropdown */
.widget-header-dropdown { @include transition-cheetah; background-color: $color-text-1; left: 0; margin-left: 0; margin-right: 0; margin-top: -10px; opacity: 0; padding: 0 20px; position: absolute; right: 0; top: 100%; visibility: hidden; z-index: -1;

    /* Show dropdown on top-level hover */
    .top-item:hover & { margin-top: 0; opacity: 1; top: 100%; visibility: visible; z-index: 1; }
    .view-all { @include transition-fast; display: block; padding: 10px 0; text-align: center; 
        &:hover { background-color: $color-primary; }
    }
}

.widget-header-nav-sub { padding-top: 20px;
    .title-sub { border-bottom: 1px solid $color-gray-4; color: $color-text-gray-4;
        a { @include transition-fast; display: block; padding: 0 20px 20px;
            &:hover { color: $color-primary; }
        }
    }
    .sub { margin-bottom: 10px; }
    .sub-item { align-items: center; display: flex; margin-top: 15px; }
    .sub-logo { margin-right: -5px; max-height: 60px; max-width: 60px; }
    .sub-text { flex: 1; margin-left: 20px; }
    .sub-name { font-size: $font-body-xm; font-weight: bold;
        a { @include transition-fast; display: block;
            &:hover { color: $color-primary; }
        }
    }

    .sub-tabs { color: $color-text-gray-3; display: flex; font-size: $font-body-sm; margin-top: 3px;
        li {
            &::after { content: '/'; display: inline-block; margin: 0 4px; }
            &:last-child::after { content: normal; }
        }
        a { @include transition-fast; 
            &:hover { color: $color-primary; }
        }
    }

    
}