/************************************************
 * Simple radio-based poll
 ************************************************/
.widget-poll { background-color: $color-bg;

    .poll-title { padding: 10px 10px 20px; }

    /* Answers */
    .poll-form { padding: 20px 10px 0; text-align: center; }
    .poll-answers { padding: 0 10px; text-align: left; }
    .poll-entry { margin-bottom: 15px; position: relative; }
    .poll-label { cursor: pointer; position: relative; top: 2px; }

    /* Results */
    .poll-result-label { margin-top: 20px; }
    .poll-result-progress { margin-top: 6px; position: relative; }
    .poll-result-fill { background-color: $color-primary; border-radius: 3px; height: 5px; position: relative; z-index: 1; }
    .poll-result-rail { background-color: $color-bg-1; content: ''; border-radius: 3px; height: 100%; left: 0; position: absolute; top: 0; width: 100%; }
    .poll-result-percent { color: $color-primary; font-weight: bold; position: absolute; right: 0; top: 0; }
    .poll-result-count { color: $color-text-secondary; font-size: $font-body-sm; margin-top: 20px; text-align: center; }

    .widget-view-more { padding-bottom: 20px; }
}