/************************************************
 * Stacked news in sidebar
 ************************************************/
.widget-news-sidebar { background-color: $color-bg; border: 1px solid $color-gray-1; margin-top: 30px;

    .item { display: block; position: relative; 

        &:hover {
            .category, .title { color: $color-primary; }
        }
    }
    .photo { display: block; }

    .text-wrap { padding: 5px 10px 20px; position: relative; }
    .category { @include transition-fast; color: $color-text-gray-3; font-size: $font-body-sm; display: block; line-height: 1.5em; margin: 2px 0; text-transform: uppercase; }
    .title { @include transition-fast; }
    .time { @include transition-fast; color: $color-text-gray-3; display: block; font-size: $font-body-sm; margin-top: 5px; }
    /* Remove from favorites, as in Video sidebar */
    .favorite-remove { background: none; border: none; cursor: pointer; display: block; height: 30px; padding: 0; position: absolute; right: 0; top: 0; width: 30px; z-index: 2; 

        .icon { @include transition-fast; transform: rotate(-45deg); }
        &:hover .icon { transform: rotate(45deg); }
    }

    .tab-content & { border: none; margin-top: 10px; }
}

.box-generic {
    .widget-news-sidebar { background-color: transparent; border: none; margin-top: 10px; }
}
.box-boulevard, .box-gray {
    .widget-news-sidebar .time { color: $color-text-secondary; margin-top: 5px; }
}