/************************************************
 * Vertical image tabs, as in Home
 ************************************************/
.widget-news-vertical { display: flex; flex-direction: row-reverse; margin-top: 30px;

    .tab-content { width: 85%; }
    .tab-item { display: none; position: relative;
        .title { background-color: $color-text-opaque; bottom: 0; color: $color-text-inverted; left: 0; padding: 10px; position: absolute; right: 0; }
        .photo { display: block; }
    }

    .tab-nav { display: flex; flex-direction: column; width: 15%;
        li { height: 20%; position: relative;

            &::before { @include transition-fast; bottom: 0; content: ''; display: block; left: 0; position: absolute; top: 0; width: 4px; z-index: 1; }
            &.active::before { background-color: $color-primary; }
        }
        a { background-position: center; background-repeat: no-repeat; background-size: cover; display: block; height: 100%; }
    }
}