/************************************************
 * Popular categories
 ************************************************/
.widget-header-popular { background-color: $color-bg-header-1; color: $color-text-inverted; font-size: $font-body-sm;

    .wrap-width { border-bottom: 1px solid $color-primary; padding: 5px 0; }
    h4 { color: $color-primary; display: inline-block; margin-right: 8px; }
    ul { display: inline-block; }
    li { display: inline-block; 
        /* Delimiter */
        &::after { color: $color-text-gray-4; content: '/'; display: inline-block; margin: 0 4px; }
        &:last-child::after { content: normal; }
    }
    a { @include transition-fast; 

        &:hover { color: $color-text-gray-2; }
    }
}