/************************************************
 * Match inside an article, as in News
 ************************************************/
.widget-match-inline { font-size: $font-body-md; margin-top: 30px;

    &:first-child { margin-top: 0; }

    .match-heading { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; color: $color-text-secondary; margin-left: -10px; margin-right: -10px; padding: 16px 10px; }

    /* Match Details */
    .match-info { padding: 15px 20px; position: relative; text-align: center; }
    .match-league { color: $color-primary; font-size: $font-body-lg; font-weight: bold; }
    .match-week { color: $color-text-secondary; font-size: $font-body-sm; margin-top: 5px; }
    .watch-live { position: absolute; right: 0; top: 15px;
        .icon { vertical-align: middle; }
        .label { color: $color-live; font-size: $font-body-md; margin-right: 5px; }
    }

    .match-teams { align-items: center; display: flex; justify-content: center; margin-top: 10px; }
    .match-team { display: flex; align-items: center;

        &:first-child { text-align: right; }
        &:last-child { text-align: left; }
    }
    .team-name { font-size: $font-body-md; font-weight: bold; margin: 0 10px; }
    .team-logo { max-height: 70px; max-width: 70px; }

    .match-status { width: 220px; }
    .match-result { font-size: $font-heading-xm; font-weight: bold; }
    .match-time { color: $color-text-secondary; font-size: $font-body-sm; margin-top: 5px; }


    /* Stadium / Referee */
    .match-inline-meta { text-align: center;
        &:last-of-type { margin-bottom: 15px; }

        .content { font-weight: bold; }
    }

    .match-lineups { margin-bottom: 30px; position: relative; text-align: center; }
    .match-system { font-weight: bold; position: absolute; }
    .match-system-left { left: 10%; }
    .match-system-right { right: 10%; }
    
    .match-coaches { display: flex; justify-content: space-between; margin-top: 5px; }
    .match-coach { align-items: center; display: flex;
        
        .photo { border-radius: 30px; max-height: 60px; max-width: 60px; }
        .name { font-weight: bold; margin: 0 10px; }
    }

    /* Generic section title */
    .match-inline-heading { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; color: $color-text-secondary; height: 40px; line-height: 40px; padding: 0 10px; }

    /* Goals */
    .match-goals { background-color: $color-bg-1; margin-left: -10px; margin-right: -10px; padding-bottom: 20px; }
    .match-goals-list { font-size: $font-body-md; }
    .match-goals-item { align-items: center; display: flex; }

    .goal-player { align-items: center; background-color: $color-bg; display: flex; flex: 1; height: 50px; margin-bottom: 2px; padding: 0 10px;

        &:first-child { justify-content: flex-end; 
            .replay { justify-self: flex-start; margin-right: auto; }
        }
        &:last-child { justify-content: flex-start; 
            .replay { justify-self: flex-end; margin-left: auto; }
        }

        .name { font-weight: bold; margin-left: 10px; }
        .photo { border-radius: 20px; margin: 0 10px; max-height: 40px; max-width: 40px; }
        .replay { line-height: 1px; }
        .minute { color: $color-text-secondary; }
    }
    .goal-result { background-color: $color-bg-2; font-weight: bold; height: 50px; line-height: 50px; text-align: center; width: 60px; }
}