.widget-f1-race-calendar {

    .race-track { align-items: center; border: 1px solid $color-gray-1; border-bottom: none; display: flex; min-height: 80px; padding: 5px 10px; }
    .race-picture { margin-right: 10px; }
    .race-text {  }
    .race-name { font-weight: bold; line-height: 1em; }
    .race-location {  }

    .race-list { border: 1px solid $color-gray-1; }
    .race-entry { display: flex; padding: 14px 15px; position: relative;
        &:nth-of-type(2n+1) { background-color: $color-bg-1; }
        &::after { content: ''; height: 100%; left: 0; position: absolute; top: 0; width: 5px; }
    }
    .race-finished {
        &::after { background-color: $color-finished; }
    }
    .race-live { font-weight: bold;
        &::after { background-color: $color-live; }
    }

    .race-activity { flex: 1; }
    .race-date { flex: 1; }
    .race-time {  }
}