.widget-top-transfers { background-color: $color-bg; margin-bottom: 30px; margin-top: 30px;

    .tf-table { border: 1px solid $color-gray-1;  width: 100%; }
    .tf-table-head { border-bottom: 1px solid $color-gray-1; color: $color-text-secondary; display: flex; justify-content: space-between; padding: 6px 10px;
        .row>* { padding-bottom: 5px; padding-top: 7px; }
    }
    .tf-table-foot { border-top: 1px solid $color-gray-1; display: flex; }
    .tf-table-body {
        .row { display: flex; padding: 10px; }
        .row:nth-child(2n+1) { background-color: $color-bg-1; }
    }

    .tf-player { display: flex;
        .photo { border-radius: 30px; float: left; margin-right: 10px; max-height: 60px; max-width: 60px; overflow: hidden; }
        .text { flex: 1; padding-top: 15px; }
        .name { display: block; font-weight: bold; }
        .team { display: block; }
    }
    .tf-price { font-weight: bold; margin-left: auto; padding-top: 15px; }

    .widget-view-more { flex: 1; margin-bottom: 10px; margin-top: 10px; padding: 10px; }
}