/************************************************
 * Embedded prediction poll
 ************************************************/
.widget-prediction-poll { background-color: $color-bg; margin-top: 30px;

    .prediction-heading { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; color: $color-text-secondary; margin-left: -10px; margin-right: -10px; padding: 16px 10px; }
    .prediction-content { padding: 20px; position: relative; text-align: center; }

    .match-league { color: $color-primary; font-size: $font-body-lg; font-weight: bold; }
    .match-week { color: $color-text-secondary; font-size: $font-body-sm; margin-top: 5px; }
    .watch-live { position: absolute; right: 0; top: 15px;
        .icon { vertical-align: middle; }
        .label { color: $color-live; font-size: $font-body-md; margin-right: 5px; }
    }

    .match-teams { align-items: center; display: flex; justify-content: center; margin-top: 10px; }
    .match-team { display: flex; align-items: center;

        &:first-child { text-align: right; }
        &:last-child { text-align: left; }
    }
    .team-name { font-size: $font-body-md; font-weight: bold; margin: 0 10px; }
    .team-logo { max-height: 70px; max-width: 70px; }

    .match-status { width: 220px; }
    .match-result { font-size: $font-heading-xm; font-weight: bold; }
    .match-time { color: $color-text-secondary; font-size: $font-body-sm; margin-top: 5px; }


    /* Vote */
    .prediction-vote { margin-top: 30px;

        .vote-heading { font-size: $font-body-xm; line-height: 1em; }
        .vote-panel { display: flex; justify-content: center; margin-top: 30px; }
        .vote-item { list-style: none; margin: 0 5px; }
        .vote-button { @include transition-fast; background: none; border: 3px solid transparent; border-radius: 25px; color: $color-text-inverted; font-size: $font-heading-md; height: 50px; line-height: 42px; padding: 0; width: 130px; }
        .vote-home { background-color: $color-live;
            &:hover { background-color: $color-bg; border-color: $color-live; color: $color-live; }
        }
        .vote-draw { background-color: $color-gray-3; 
            &:hover { background-color: $color-bg; border-color: $color-gray-3; color: $color-text-gray-3; }
        }
        .vote-away { background-color: $color-finished; 
            &:hover { background-color: $color-bg; border-color: $color-finished; color: $color-finished; }
        }
    }
    .vote-count { color: $color-text-secondary; margin-top: 20px; }
    .vote-count-results { margin-top: 12px; }

    /* Results */
    .vote-results { display: flex; margin: 20px auto 0; width: 600px; }
    .vote-results-item { margin: 0 5px; position: relative;
        .icon { margin-bottom: 5px; }
        .bar { color: $color-text-inverted; height: 40px; line-height: 40px; padding: 0 10px; }
        .name { font-size: $font-body-xm; font-weight: bold; margin-top: 15px; position: absolute; }
    }
    .vote-results-home { text-align: left;
        .bar { background-color: $color-live; border-radius: 8px 0 0 8px; }
        .name { left: 0; }
    }
    .vote-results-draw {
        .bar { background-color: $color-gray-3; }
    }
    .vote-results-away { text-align: right;
        .bar { background-color: $color-primary; border-radius: 0 8px 8px 0; }
        .name { right: 0; }
    }
    
}