.box-generic { background-color: $color-bg; border: 1px solid $color-gray-1;

    /* Part of a colum, and not a wrapper */
    .grid & { margin-top: 30px; }

    /* After a heading - no spacing needed */
    .widget-heading-small + &, .widget-heading-large + & { margin-top: 0; }
}

.box-boulevard { background-color: $color-bg-boulevard; border-color: $color-boulevard-dark; }
.box-gray { background-color: $color-bg-3; }