.widget-gallery-main {

    .gallery-title {
        .title { margin-bottom: 15px; }
        .time { color: $color-text-gray-3; display: inline-block; float: left; font-size: $font-body-sm; }
    }

    .gallery-counter { display: inline-block; float: right;

        &>* { vertical-align: middle; }
        .current { font-weight: bold; }
        .divider {  }
        .total {  }
        .icon { margin-left: 20px; }
    }

    /* Main carousel */
    .carousel-gallery-main { margin-top: 15px; position: relative;

        .content {
            img { display: block; }
        }

        .arrows {
            a { @include transition-fast; background-position: center; background-repeat: no-repeat; height: 100%; position: absolute; top: 0; width: 55px; }

            .prev { background-image: url(../image/icon/ar_previous_big_normal.png); left: 0; 
                &:hover { background-image: url(../image/icon/ar_previous_big_hover.png); left: 5px; }
            }
            .next { background-image: url(../image/icon/ar_next_big_normal.png); right: 0; 
                &:hover { background-image: url(../image/icon/ar_next_big_hover.png); right: 5px;; }
            }
        }
    }

    .gallery-author { margin-left: auto; }
    .gallery-item-text { font-size: $font-body-xm; margin-top: 16px; }

    /* Thumbail carousel */
    .carousel-gallery-thumbs { margin-top: 25px;

        .wrap { position: relative; }
        .content { display: flex; flex-wrap: wrap; margin: 0 40px;
            .entry { margin: 0 2.5px 2.5px; width: calc(20% - 5px); }
            img { display: block; }
        }
        .slick-list { width: 100%; }

        .arrows {
            a { @include transition-fast; background-position: center; background-repeat: no-repeat; height: 100%; position: absolute; top: 0; width: 35px; }

            .prev { background-image: url(../image/icon/ar_previous_normal.png); left: 0; 
                &:hover { background-image: url(../image/icon/ar_previous_hover.png); }
            }
            .next { background-image: url(../image/icon/ar_next_normal.png); right: 0; 
                &:hover { background-image: url(../image/icon/ar_next_hover.png); }
            }
        }

        /* Expanded thumbs - arrows not needed */
        &.thumbs-opened .arrows { opacity: 0; visibility: hidden; }

    }

    /* View More button */
    .widget-view-more-load { margin-bottom: 0; margin-top: 15px; 

        &.thumbs-opened {
            .icon { transform: rotate(180deg); }
        }
    }
}

/* More photo galleries */
.widget-gallery-grid {

    .item { margin-bottom: 20px; margin-top: 10px; }
    /* More Photos grid - items in gallery */
    .gallery-grid-action { margin-bottom: 10px; padding-left: 10px;

        .icon { @include transition-fast; vertical-align: middle; 
            /* Remove favorite */
            &.added { transform: rotate(45deg); }
        }
        &:hover .icon-add { transform: rotate(90deg);
            &.added { transform: rotate(-45deg); }
        }

        .label { @include transition-fast; line-height: 1.2em; margin-left: 7px; vertical-align: middle; }
        strong { font-weight: bold; }
    }
    .item:hover {
        .gallery-grid-action {
            /* Only change Gallery's photo counters and label, not Videos' "Add to My Favorite" */
            .icon-photo { background-image: url(../image/icon/i_photo_hover.png); 
                &+.label { color: $color-primary; }
            }
        }
    }
}

/* Inline photo with gallery items count */
.widget-gallery-trigger { display: block; margin: 30px 0; position: relative;
    .trigger-photo { display: block; }
}
.gallery-photos-count { background-color: $color-text-opaque; border-radius: 5px; bottom: 10px; left: 10px; padding: 3px 20px 3px 10px; position: absolute;
    .icon { position: relative; top: -2px; vertical-align: middle; }
    .label { color: $color-text-inverted; display: inline-block; font-size: $font-body-lg; font-weight: bold; margin-left: 7px; vertical-align: middle; }
}