/* Team vs Team logos/names */
.match-teams-generic { align-items: center; display: flex; flex-wrap: wrap; justify-content: center; margin-bottom: 10px; margin-top: 20px;

    .match-team { display: flex; flex: 1; align-items: center;

        &:first-child { justify-content: flex-end; text-align: right; }
        &:last-child { text-align: left; }
    }
    .team-name { font-size: $font-body-md; font-weight: bold; margin: 0 10px; }
	.team-logo { max-height: 70px; max-width: 70px; }
	.spacer { width: 130px; }

	.match-status { margin-top: 15px; text-align: center; width: 100%; }
	.match-count { color: $color-primary; font-size: $font-heading-xxl; font-weight: bold; line-height: 1em; margin-bottom: 5px; padding-bottom: 5px; position: relative;
		/* Divider line */
		&::after { background-color: $color-primary; content: ''; bottom: 0; display: block; height: 2px; left: 50%; margin-left: -135px; position: absolute; width: 270px; }
	}
}

/* Team side-by-side comparisons */
.match-compare-generic { display: flex; padding: 10px;

	&.main {
		.bar, .team, .center { position: relative;
			&::after { background-color: $color-gray-1; bottom: 0; content: ''; height: 1px; left: 0; position: absolute; width: 100%; } 
		}
	}
	&.footer {
		.bar, .team { align-self: flex-start; }
	}

	.bar { display: flex; flex-direction: column-reverse; margin: 0 5px; position: relative; width: 70px; 
		.match-symbols { align-self: flex-start; justify-self: flex-start; left: 50%; position: relative; transform: translateX(-50%); white-space: nowrap; }
	}
	.bar-label { font-size: $font-body-lg; font-weight: bold; height: 30px; line-height: 30px; text-align: center; }
	.bar-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; }
	.bar-labels { align-items: center; color: $color-text-gray-3; display: flex; flex-direction: column; height: 100%; padding-top: 30px; text-align: center; }
	.bar-labels-item { flex: 1; }
	

	.team { display: flex; flex: 1; flex-direction: column; justify-content: flex-end; margin-right: 5px; padding: 0 15px 10px; 
		&:last-of-type { align-items: flex-end; margin-left: 5px; margin-right: 0; }
	}
	.team-league { align-items: center; display: flex;
		.name { font-weight: bold; margin: 0 10px; }
		.logo { height: 30px; width: 30px; }
	}

	.center { align-items: flex-end; display: flex; justify-content: center; margin: 0 5px; width: 110px; 
		img { margin-bottom: 10px; }
	}
}

.match-season-text { margin-bottom: 15px; text-align: center;
	.content { font-weight: bold; }
}

/* Circular W, D, L symbols */
.match-symbols { text-align: center;
	.match-outcome { @include circle(20); color: $color-text-inverted; display: inline-block; font-size: $font-body-xs; }
	.match-w { background-color: $color-live; }
    .match-d { background-color: $color-gray-3; }
	.match-l { background-color: $color-finished; }
}

/* Head 2 Head */
.widget-match-head { margin-top: 30px;

	/* Pie charts */
	.match-chart { display: flex; justify-content: center; padding-bottom: 60px;
		.text { display: inline-block; width: 150px; }
		.count { border-bottom: 1px solid $color-gray-1; display: inline-block; font-size: $font-body-lg; font-weight: bold; line-height: 30px; padding: 0 10px; width: 50px; }
		.label { display: block; padding: 5px 10px; }

		.circle { height: 100px; display: inline-block; position: absolute; top: 50px; width: 100px; }
		.circle-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; border-radius: 50%; display: inline-block; left: 50%; position: absolute; top: 50%; transform: translate(-50%, -50%);

			/* Diagonal line */
			&::before { background-color: $color-gray-1; content: ''; display: inline-block; height: 1px; position: absolute; top: 50%; width: 100px; z-index: 1; }

			/* Central dot */
			&::after { @include circle(4); background-color: $color-text; content: ''; left: 50%; margin-left: -2px; margin-top: -2px; position: absolute; top: 50%; z-index: 1; }
		}
	}
	.match-chart-left { flex: 1; padding-right: 120px; position: relative; text-align: right;
		.circle { right: 0; }
		.circle-fill::before { right: 50%; transform: rotate(45deg); transform-origin: bottom right; }
	}
	.match-chart-right { flex: 1; padding-left: 120px; position: relative;
		.circle { left: 0; }
		.circle-fill::before { left: 50%; transform: rotate(-45deg); transform-origin: bottom left; }
	}
	.match-chart-center { margin-top: 45px; position: relative; text-align: center; width: 100px;
		.count { border-bottom: none; margin-bottom: 45px; }
		.circle { height: 50px; left: 50%; margin-left: -25px; top: 30px; width: 50px; }
		.circle-fill::before { content: normal; }
	}

	/* Match statistics */
	.match-stats { background-color: $color-bg-1; margin-bottom: 20px; padding: 15px;

		.entry { line-height: 1.5em; margin-bottom: 3px; }
		.label { display: inline-block; text-align: right; width: 45%; }
		.divider { @include circle(6); background-color: $color-primary; margin: 0 18px; vertical-align: middle; }
		.content { display: inline-block; font-weight: bold; width: 45%; 
			a { @include transition-fast; }
			a:hover { color: $color-primary; }
		}
	}
}

/* Goals */
.widget-match-goals { margin-top: 30px;
	
	.match-stats { background-color: $color-bg-1; margin: 20px 0; padding: 15px; text-align: center;
		.entry { display: flex; justify-content: center; }
		.label { margin: 0 5px; width: 150px; }
		.content { margin: 0 5px; width: 30px; font-weight: bold; }
		.won { color: $color-primary }
	}
}

.widget-match-form { margin-top: 30px;
	.bar-labels-item { border-top: 1px solid $color-gray-1; font-size: $font-body-sm; width: 70px; }
}

.widget-match-ranking { margin-top: 30px;
	.bar { flex-direction: column; }
	.bar-item { background-color: $color-bg-1; border: 1px solid $color-gray-1; font-size: $font-heading-md; height: 60px; line-height: 60px; margin-top: -1px; text-align: center;
		
		&.primary { background-color: $color-primary; border-color: $color-primary; color: $color-text-inverted; font-weight: bold; }
		&.secondary { background-color: $color-bg; color: $color-primary; font-weight: bold; }
	}
	.bar-labels { justify-content: center; padding-top: 0; }
	.bar-labels-item { @include vertical-align; }
}

/* Top scorers */
.widget-match-topscorers { margin-top: 30px;

	.box-generic { padding-bottom: 10px; }

	.match-people { display: flex; justify-content: center; margin-bottom: 20px; padding: 0 10px; }
	.match-col { border-bottom: 1px solid $color-gray-1; flex: 1; padding-bottom: 10px; }
	.match-people-spacer { align-items: center; border-bottom: 1px solid $color-gray-1; display: flex; flex-direction: column; justify-content: flex-end; margin: 0 10px; padding-bottom: 10px; width: 150px; }

	.player-entry { display: flex; margin-bottom: 10px; }
	.player-text { align-self: flex-end; flex: 1; padding-bottom: 5px; }
	.player-name { font-weight: bold; line-height: 1em; }
	.player-image { line-height: 1px; position: relative; }
	.player-photo { border-radius: 50%; height: 60px; margin: 0 10px; vertical-align: middle; width: 60px; }
	.player-rank { @include circle(30); background-color: $color-primary; color: $color-text-inverted; vertical-align: middle; }

	.match-people-left { text-align: right; }
}