.widget-team-vote { margin-top: 30px; padding: 0 10px;

	.title { margin-bottom: 15px; }
	.vote-wrap { align-items: center; display: flex; }
	.vote-rating { align-items: center; border-right: 1px solid $color-gray-1; bottom: 0; display: flex; flex: 1; left: 0; margin-right: 20px;
		.value { background-color: $color-primary; color: white; font-size: $font-heading-xxl; font-weight: bold; height: 90px; line-height: 70px; margin-right: 20px; padding: 10px; text-align: center; width: 110px; }
	}
	.vote-action { flex: 1; }
	.vote-count { margin-top: 5px; text-align: center; }

	.vote-results { flex: 1; }
	.vote-results-row { align-items: center; display: flex; }
	.vote-results-stars { text-align: right; width: 70px; }
	.vote-results-track { background-color: $color-bg; display: block; height: 2px; margin-left: 20px; width: 200px; }
	.vote-results-fill { background-color: $color-gray-4; display: block; height: 100%; }
}

/* Team Info */
.widget-team-info { background-color: $color-bg; margin-top: 30px;

	.team-properties { padding: 20px;

		.entry { line-height: 1.5em; margin-bottom: 12px; }
		.label { display: inline-block; text-align: right; width: 45%; }
		.divider { @include circle(6); background-color: $color-primary; margin: 0 18px; vertical-align: middle; }
		.content { display: inline-block; font-weight: bold; width: 45%; 
			a { @include transition-fast; }
			a:hover { color: $color-primary; }
		}
	}
	
}

// /* Next Match */
// .widget-team-match { padding-bottom: 20px; }

/* Team form - bad, good, etc. */
.widget-team-form { background-color: $color-bg; margin-top: 30px; padding-bottom: 10px;

	.bar-wrap { align-items: center; display: flex; flex-direction: column; padding: 30px 20px 20px; position: relative; left: -20px; }
	.bar-percentage { font-size: $font-body-xm; font-weight: bold; width: 55px; }
	.bar { align-items: center; display: flex; }
	.bar-labels { display: flex; height: 15px; line-height: 15px; margin-bottom: 10px; margin-left: 55px; width: 545px; }
	.bar-labels-item { border-left: 1px solid $color-gray-1; flex: 1; text-align: center;
		&:last-child { border-right: 1px solid $color-gray-1; }
	}

	.bar-track { height: 60px; position: relative; width: 545px; 
		/* Dotted border */
		&::before { border: 1px dashed $color-gray-3; bottom: 0; content: ''; left: 0; position: absolute; right: 0; top: 0; }
	}
	.bar-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; height: 100%; position: relative; }
}

/* As in Team - Overview */
.widget-team-ranking { background-color: $color-bg; margin-top: 30px;

	/* League ranking */
    .section-header { align-items: center; display: flex; height: 60px; padding: 10px 10px 5px;
       /* As in Team - Matches - Fixtures */
        .logo { max-height: 30px; max-width: 30px; }
        .name { font-weight: bold; margin-left: 10px; margin-right: 4px; }
	}
	
	/* Standings stats */
	.team-ranking-standings { background-color: $color-bg-1; padding: 20px 0 10px; 

		.list { display: flex; justify-content: center; margin-bottom: 10px; }
		.entry { text-align: center; width: 110px; 
			&:last-child .bar { border-right: 1px solid $color-gray-1; }
		}
		.bar { border: 1px solid $color-gray-1; border-right: none; display: block; font-size: $font-heading-md; height: 60px; line-height: 60px; margin-top: 7px; }
	
		.primary .bar { background-color: $color-primary; border-color: $color-primary; color: $color-text-inverted; font-weight: bold; }
		.secondary .bar { background-color: $color-bg; color: $color-primary; font-weight: bold; }
	}

	/* Top scorer, etc. */
	.team-ranking-players { margin-top: 20px; padding: 10px;

		.entry { margin-bottom: 25px; width: 50%; }
		.label { font-size: $font-body-xm; font-weight: bold; margin-bottom: 8px; padding-left: 10px; }
		.box { background-color: $color-bg-1; align-items: flex-end; display: flex; padding: 20px 35px; }
		.player-image { margin-right: 8px; position: relative; }
		.player-event { bottom: 0; left: -12px; position: absolute; }
		.player-photo { border-radius: 30px; display: block; max-height: 60px; max-width: 60px; }
		.player-name { font-weight: bold; }
		.player-position { padding-bottom: 3px; }
		.player-rank { color: $color-primary; font-size: $font-heading-md; font-weight: bold; margin-left: auto; }
	}
}

.widget-team-squad { background-color: $color-bg;

	/* Season */
	.widget-category-dropdown { padding: 20px 10px 0; }
	
	.squad-header { border-bottom: 1px solid $color-gray-1; font-weight: bold; height: 30px; line-height: 30px; margin-top: 20px; padding: 0 10px; }
	.squad-list { background-color: $color-bg-1;
		.player { align-items: flex-end; display: flex; padding: 10px 20px; }
		.player-image { line-height: 1px; position: relative; }
		.player-photo { border-radius: 50%; height: 60px; margin-right: 10px; width: 60px; }
		.player-rank { @include circle(30); background-color: $color-primary; color: $color-text-inverted; margin-right: -15px; position: relative; vertical-align: bottom;  z-index: 1; }
		.player-text { align-self: flex-end; flex: 1; padding-bottom: 5px; }
		.player-name { font-weight: bold; line-height: 1em; }
	}
}