/************************************************
 * Generic tabs
 ************************************************/
.widget-tabs-default { background-color: $color-bg; border: 1px solid $color-gray-1; border-radius: 3px 3px 0 0; margin-top: 15px;

    .tab-nav { display: flex; font-weight: bold; text-transform: uppercase;
        li { flex: 1; }
        a { @include transition-fast; background-color: $color-bg-1; border: 1px solid transparent; border-radius: 3px 3px 0 0; border-bottom-color: $color-gray-1; color: $color-text-secondary; display: block; padding: 4px 0; position: relative; text-align: center; top: 0; }
        .active a { border-color: $color-gray-1; border-bottom-color: transparent; background-color: $color-bg; color: $color-text; padding: 9px 0 4px; margin-top: -5px; }
        li:first-child.active a { margin-left: -1px; }
        li:last-child.active a { margin-right: -1px; }
    }
}

.widget-tabs-small {
    a { font-size: $font-body-sm; }
}