.widget-f1-ranking-list { margin-bottom: 20px;

    .ranking-table { border-top: none; width: 100%; }
    .ranking-table-head { border-bottom: 1px solid $color-gray-1; color: $color-text-secondary; display: flex; padding: 5px 10px;

        .ranking-position {  }
        .ranking-driver {  }
        .ranking-points {  }
    }    
    .ranking-table-body {
        .ranking-row:nth-child(2n+1) { background-color: $color-bg-1; }
    }
    .ranking-row { align-items: center; display: flex; padding: 10px; }
    .ranking-position { width: 20px; }
    .ranking-driver { flex: 1; }
    .ranking-name { font-weight: bold; line-height: 1em; }


    .ranking-table-foot { border-top: 1px solid $color-gray-1; display: flex; }

    .widget-view-more { flex: 1; margin-bottom: 10px; margin-top: 10px; padding: 10px; }
}